.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  width: 100%;
  height: 100px;
  gap: 10px;
  background: black;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0.5rem;
  z-index: 1;
}
.navbar .logo{
  width: 50%;
  height: auto;
  display: flex;
  justify-content: end;
  align-items: center;
  position: relative;
}
.navbar .logo .image-logo {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: auto;
  border-radius: 100%;
}
.navbar .spacy {
  display: flex;
  justify-content: end;
  align-items: center;
  width: 100%;
  height: auto;
}
.navbar .social-media {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  color: white;
}
.navbar .nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.navbar .nav-icon {
  font-size: 1.5rem;
  color: var(--primaryColor);
}
.navbar .nav-links {
  height: 0;
  overflow: hidden;
  transition: var(--mainTransition);
  list-style-type: none;
}
.navbar .nav-links a {
  font-family: "Muli", sans-serif;
  display: block;
  text-decoration: none;
  padding: 1rem 0;
  transition: var(--mainTransition);
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: var(--mainSpacing);
  color: #ececec;
}
.navbar .nav-links a:hover {
  color: #DAB855;
}
.navbar .show-nav {
  height: 100px;
}
@media screen and (min-width: 768px) {
  .navbar .nav-btn {
    display: none;
  }
  .navbar .nav-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
  }
  .navbar .nav-links {
    height: auto;
    display: flex;
    margin-left: 4rem;
  }
  .navbar .nav-links a {
    margin: 0 1rem;
    padding: 0.5rem 0;
  }
}

